/** IMPORTS **/

@import "https://cdnjs.cloudflare.com/ajax/libs/animate.css/3.7.0/animate.min.css";


/** WEB PAGE **/

*, html, input[type=checkbox], input[type=radio] {
    box-sizing: border-box;
}

.no-decoration {
	text-decoration: none
}

body, html {
    height: 100%;
    margin: 0
}

body {
    overflow-x: hidden
}

body:after {
	background-image: none
}

#wrapper {
    overflow: hidden;
    position: relative;
    min-height: 100%
}

/** HEADER **/

header.header {
    overflow: hidden;
}


header.header .background {
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-size: cover;
    max-width: 100%;
    max-height: 100%;
    height: 100%;
    background-image: linear-gradient(to top, #9795f0 0%, #fbc8d4 100%);
    position: fixed;
    z-index: -1;
    width: 100%
}

header.header .bgfilter {
    position: fixed;
    z-index: -1;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, .4);
}

.title .headline {
	color: #2188ff;
	font-weight: 600
}

header.header div.title h2.subtitle {
    color: white;
	font-weight: 300;
	font-size: 1.3em
}


header.header div.title img {
	border-radius: 10%;
	width: 15%;
	height: 15%
}


/** MAIN **/

#main, #main:before {
    padding: 1.5em 0 1em;
    background: linear-gradient(to bottom,#f4f4f4,#fff)
}


/** POSTS **/

.posts {
    margin: 40px 0 40px 5%
}

.posts img.avatar {
    display: inline-block;
    margin: 0;
    width: 2em;
    height: 2em;
    border-radius: 500rem
}

.post div.content {
    width: 80%;
    display: inline-block;
    padding: 0 0 0 .5em;
    vertical-align: top;
    line-height: 1.14285714em;
    font-size: 1em
}

.post div.content .description {
    word-break: break-word
}

.post:not(:last-child) {
    padding-top: 0;
    padding-bottom: .85714286em;
    display: list-item;
    table-layout: fixed;
    list-style-type: none;
    list-style-position: outside;
    padding: .21428571em 0;
    line-height: 1.14285714em
}

.post .header {
    display: flex;
    margin: 0;
    font-weight: 700;
    color: #4183c4!important;
    line-height: 1.14285714em;
    font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif
}

.post .header .label {
    margin: auto 0 0 1%
}

.post {
    margin-bottom: 2%;
    font-size: .8em
}

hr {
    border: 0;
    height: 1px;
    width: 25%;
    margin: 2.5rem auto;
    background-color: #ddd
}


/** EXPERIMENTS **/

.experiments-title > .exp-title,
.experiments-title > .exp-subtitle {
    text-align: center
}

.experiments-title > .exp-title {
    margin-bottom: 0
}

.experiments-title > .exp-subtitle {
    font-weight: 300;
    margin-top: 0;
    margin-bottom: 10px
}

.experiment {
    text-align: center;
    font-size: .8em;
    margin-bottom: 2em
}

.experiment .experiment-img {
    display: block;
    height: 150px;
    backface-visibility: hidden;
    border-radius: 4px 4px 0 0;
    background-size: cover;
    transition: opacity .2s
}

.experiment .experiment-info {
    padding: 1em;
    border-radius: 0 0 4px 4px;
    z-index: 3
}

.experiment .experiment-info a {
    display: block;
    font-weight: 700;
    color: inherit
}

.experiment .experiment-info .experiment-date {
    opacity: .6
}